.content {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.links {
  font-size: calc(10px + 1vmin);
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

a {
  width: calc(15px + 2vmin);
  height: calc(15px + 2vmin);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  outline-color: white;
  height: calc(15px + 2vmin);
  outline-style: solid;
  outline-width: 1px;
  color: white;
}

.title {
  font-weight: 700;
}
